
.team {
  position: absolute;
  width: 100%;
}
.team .content {
    box-sizing: border-box;
    width: 1200px;
    margin: auto;
    padding: 60px 30px 150px 30px;
}
.team .content .intro {
      width: 784px;
      margin: auto;
      margin-bottom: 100px;
}
.team .content .intro .title {
        text-align: center;
        font-weight: normal;
        font-size: 20px;
        color: #c1c7d4;
        margin-bottom: 30px;
}
.team .content .intro .text {
        font-size: 14px;
        color: #323232;
        line-height: 22.65px;
}
.team .content .intro .text.center {
          text-align: center;
}
.team .content .team-intro .team-card-container {
      position: relative;
      height: 330px;
      margin-bottom: 50px;
}
.team .content .team-intro .team-card-container .team-card {
        position: absolute;
        top: 0;
        width: 355px;
        height: 330px;
        padding: 64px 30px 42px 30px;
        box-sizing: border-box;
        background: #fff;
        box-shadow: 0 5px 25px 0 rgba(100, 146, 188, 0.15);
        transition: box-shadow 0.2s, left 0.6s, -webkit-transform 0.2s;
        transition: box-shadow 0.2s, transform 0.2s, left 0.6s;
        transition: box-shadow 0.2s, transform 0.2s, left 0.6s, -webkit-transform 0.2s;
        cursor: pointer;
}
.team .content .team-intro .team-card-container .team-card:hover, .team .content .team-intro .team-card-container .team-card.active {
          box-shadow: 0 20px 60px 0 rgba(100, 146, 188, 0.3);
          -webkit-transform: translateY(-5px);
                  transform: translateY(-5px);
          z-index: 1;
}
.team .content .team-intro .team-card-container .team-card.left {
          left: 0;
}
.team .content .team-intro .team-card-container .team-card.center {
          left: 393px;
}
.team .content .team-intro .team-card-container .team-card.right {
          left: 785px;
}
.team .content .team-intro .team-card-container .team-card .icon {
          width: 64px;
          height: 64px;
          margin: auto;
          margin-bottom: 30px;
}
.team .content .team-intro .team-card-container .team-card .icon.team0 {
            background: url(../assets/team0.png) center no-repeat;
}
.team .content .team-intro .team-card-container .team-card .icon.team1 {
            background: url(../assets/team1.png) center no-repeat;
}
.team .content .team-intro .team-card-container .team-card .icon.team2 {
            background: url(../assets/team2.png) center no-repeat;
}
.team .content .team-intro .team-card-container .team-card .title {
          color: #323232;
          font-size: 20px;
          line-height: 20px;
          text-align: center;
          margin-bottom: 20px;
}
.team .content .team-intro .team-card-container .team-card .text {
          color: #c1c7d4;
          line-height: 22.65px;
          font-size: 14px;
}
.team .content .team-intro .line {
      width: 2px;
      height: 130px;
      background: #ff0042;
      margin: auto;
      margin-bottom: 50px;
}
.team .content .team-intro .info-container {
      position: relative;
}
.team .content .team-intro .info-container .show-container {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        width: 900px;
        height: 203px;
        margin: auto;
        overflow: hidden;
}
.team .content .team-intro .info-container .show-container .content-wrapper {
          display: inline-block;
          margin: 0 auto;
          transition: 0.5s;
}
.team .content .team-intro .info-container .show-container .content-wrapper.slide-left-leave-active {
            -webkit-transform: translateX(-900px);
                    transform: translateX(-900px);
}
.team .content .team-intro .info-container .show-container .content-wrapper.slide-left-enter {
            -webkit-transform: translateX(900px);
                    transform: translateX(900px);
}
.team .content .team-intro .info-container .show-container .content-wrapper.slide-right-leave-active {
            -webkit-transform: translateX(900px);
                    transform: translateX(900px);
}
.team .content .team-intro .info-container .show-container .content-wrapper.slide-right-enter {
            -webkit-transform: translateX(-900px);
                    transform: translateX(-900px);
}
.team .content .team-intro .info-container .show-container .content-wrapper .avatar {
            width: 50px;
            height: 50px;
            margin: auto;
            margin-bottom: 15px;
            border-radius: 50%;
            overflow: hidden;
}
.team .content .team-intro .info-container .show-container .content-wrapper .avatar .img {
              width: 50px;
              height: 50px;
}
.team .content .team-intro .info-container .show-container .content-wrapper .name {
            text-align: center;
            font-size: 16px;
            line-height: 16px;
            color: #323232;
            margin-bottom: 10px;
}
.team .content .team-intro .info-container .show-container .content-wrapper .team-text {
            text-align: center;
            font-size: 14px;
            line-height: 14px;
            color: #c1c7d4;
            margin-bottom: 30px;
}
.team .content .team-intro .info-container .show-container .content-wrapper .intro {
            width: 784px;
            margin: auto;
            text-align: center;
            font-size: 14px;
            line-height: 22.65px;
            color: #323232;
}
.team .content .team-intro .info-container .btn {
        position: absolute;
        top: 83px;
        width: 60px;
        height: 60px;
        border: none;
        border-radius: 50%;
        transition: 0.2s;
        background: url(../assets/bigArrow.png) no-repeat center center;
        cursor: pointer;
}
.team .content .team-intro .info-container .btn.left {
          left: 0;
          -webkit-transform: rotate(180deg);
                  transform: rotate(180deg);
}
.team .content .team-intro .info-container .btn.right {
          right: 0;
}
.team .content .team-intro .info-container .btn:active {
          outline: 2px solid #13A7FF;
}
.team .content .team-intro .info-container .btn:focus {
          outline: none;
}



