
.contact {
  position: absolute;
  width: 100%;
}
.contact .text-delete {
    text-decoration: line-through;
}
.contact .content {
    box-sizing: border-box;
    width: 1200px;
    margin: auto;
    padding: 60px 30px 150px 30px;
}
.contact .content .intro {
      width: 784px;
      margin: auto;
      margin-bottom: 100px;
}
.contact .content .intro .title {
        text-align: center;
        font-weight: normal;
        font-size: 20px;
        color: #c1c7d4;
        margin-bottom: 30px;
}
.contact .content .intro .text {
        font-size: 14px;
        color: #323232;
        line-height: 22.65px;
}
.contact .content .form {
      box-sizing: border-box;
      padding: 100px 60px;
      background: #fff;
      box-shadow: 0 0 60px 0 rgba(100, 146, 188, 0.3);
}
.contact .content .form .row1 {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
        margin-bottom: 60px;
}
.contact .content .form .row1 .title {
          font-size: 20px;
          line-height: 20px;
          color: #1e2022;
          margin-bottom: 60px;
}
.contact .content .form .row1 .personal-info {
          width: 450px;
}
.contact .content .form .row1 .personal-info .text-input {
            position: relative;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: reverse;
                -ms-flex-direction: column-reverse;
                    flex-direction: column-reverse;
            margin-bottom: 60px;
}
.contact .content .form .row1 .personal-info .text-input:last-child {
              margin-bottom: 0;
}
.contact .content .form .row1 .personal-info .text-input .error-message {
              position: absolute;
              right: 0;
              top: 0;
              line-height: 20px;
              font-size: 14px;
              color: #ff0042;
}
.contact .content .form .row1 .personal-info .text-input .subtitle {
              line-height: 20px;
              font-size: 14px;
              color: #c1c7d4;
              margin-bottom: 30px;
              line-height: 14px;
              transition: 0.2s;
}
.contact .content .form .row1 .personal-info .text-input .input {
              width: 100%;
              border: none;
              border-bottom: 2px solid #c1c7d4;
              padding-bottom: 10px;
              outline: none;
              font-size: 16px;
              color: #1e2022;
              transition: 0.2s;
}
.contact .content .form .row1 .personal-info .text-input .input:focus {
                border-bottom: 2px solid #ff0042;
}
.contact .content .form .row1 .personal-info .text-input .input:focus + .subtitle {
                color: #ff0042;
}
.contact .content .form .row1 .contact-info {
          position: relative;
          width: 450px;
}
.contact .content .form .row1 .contact-info .item {
            margin-bottom: 72px;
}
.contact .content .form .row1 .contact-info .item:last-child {
              margin-bottom: 0;
}
.contact .content .form .row1 .contact-info .item .subtitle {
              line-height: 20px;
              font-size: 14px;
              color: #c1c7d4;
              margin-bottom: 30px;
              line-height: 14px;
}
.contact .content .form .row1 .contact-info .item .subtitle .error-message {
                float: right;
}
.contact .content .form .row1 .contact-info .item .text {
              font-size: 16px;
              color: #ff0042;
              line-height: 16px;
}
.contact .content .form .row1 .contact-info .QR {
            position: absolute;
            right: 0;
            top: 121px;
            width: 150px;
            height: 150px;
}
.contact .content .form .row1 .contact-info .QR .qr-img {
              widows: 150px;
              height: 150px;
}
.contact .content .form .row2 .textarea-container {
        position: relative;
        margin-bottom: 60px;
}
.contact .content .form .row2 .subtitle {
        line-height: 20px;
        font-size: 14px;
        color: #c1c7d4;
        margin-bottom: 30px;
        line-height: 14px;
        transition: 0.2s;
}
.contact .content .form .row2 .subtitle.focus {
          color: #ff0042;
}
.contact .content .form .row2 .error-message {
        position: absolute;
        right: 0;
        top: 0;
        line-height: 20px;
        font-size: 14px;
        color: #ff0042;
}
.contact .content .form .row2 .expanding-area {
        position: relative;
        width: 100%;
        padding-bottom: 10px;
        border-bottom: 2px solid #c1c7d4;
}
.contact .content .form .row2 .expanding-area.focus {
          border-bottom: 2px solid #ff0042;
}
.contact .content .form .row2 .expanding-area pre {
          display: block;
          font-size: 16px;
          line-height: 24px;
          visibility: hidden;
}
.contact .content .form .row2 .expanding-area .textarea {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          bottom: 0;
          padding: 0;
          overflow: hidden;
          resize: none;
          border: none;
          outline: none;
          font-size: 16px;
          white-space: pre-wrap;
          word-wrap: break-word;
          line-height: 24px;
          color: #1e2022;
          transition: 0.2s;
}
.contact .content .form .row2 .btn {
        display: inline-block;
        box-sizing: content-box;
        text-align: center;
        width: 94px;
        height: 32px;
        line-height: 32px;
        background: #fff;
        border: 1px solid #ff0042;
        border-radius: 53px;
        font-size: 14px;
        color: #1e2022;
        cursor: pointer;
        transition: 0.2s;
}
.contact .content .form .row2 .btn:hover {
          background: #ff0042;
          color: #fff;
}
.contact .content .form .row2 .btn.processing {
          padding-left: 10px;
          background: #ff0042;
          color: #fff;
          cursor: default;
}
.contact .content .form .row2 .btn.processing .icon {
            display: inline-block;
            vertical-align: -2px;
            width: 16px;
            height: 16px;
            margin-left: -16px;
}
.contact .content .form .row2 .btn.processing .icon.loading {
              background: url("../assets/loading.png") no-repeat;
              -webkit-animation: circle 1s infinite linear;
                      animation: circle 1s infinite linear;
}
@-webkit-keyframes circle {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes circle {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.contact .content .form .row2 .btn.processing .icon.success {
              background: url("../assets/success.png");
}
.contact .content .form .row2 .btn.processing .icon.error {
              background: url("../assets/error.png");
}



