
@font-face {
  font-family: "PangMenZhengDao";
  src: url("../assets/PangMenZhengDao.ttf");
}
.index {
  position: absolute;
  width: 100%;
}
.index .content {
    box-sizing: border-box;
    width: 1200px;
    margin: auto;
    padding: 60px 30px 150px 30px;
}
.index .content .intro {
      width: 784px;
      margin: auto;
      margin-bottom: 100px;
}
.index .content .intro .title {
        text-align: center;
        font-weight: normal;
        font-size: 20px;
        color: #c1c7d4;
        margin-bottom: 30px;
}
.index .content .intro .text {
        font-size: 14px;
        color: #323232;
        line-height: 22.65px;
}
.index .content .intro .text.center {
          text-align: center;
}
.index .content .content-item {
      height: 630px;
      font-size: 0;
      margin-bottom: 100px;
}
.index .content .content-item .bg {
        position: absolute;
        width: 760px;
        height: 630px;
        left: 0;
        right: 0;
        margin: auto;
        background: url("../assets/Triangle.png") no-repeat;
        z-index: -1;
}
.index .content .content-item .transparent-container {
        position: relative;
        display: inline-block;
        box-sizing: border-box;
        width: 50%;
        height: 100%;
        padding: 0 48px;
        vertical-align: top;
}
.index .content .content-item .transparent-container .text {
          margin-top: 184px;
          color: #323232;
          font-size: 14px;
          line-height: 22.65px;
}
.index .content .content-item .white-container {
        position: relative;
        display: inline-block;
        box-sizing: border-box;
        width: 50%;
        height: 100%;
        padding: 10px;
        background: #fff;
        box-shadow: 0 20px 60px 0 rgba(100, 146, 188, 0.3);
}
.index .content .content-item .white-container .img-container {
          height: 380px;
          margin-bottom: 40px;
}
.index .content .content-item .white-container .img-container .img {
            width: 550px;
            height: 380px;
}
.index .content .content-item .white-container .text {
          font-size: 14px;
          color: #323232;
          line-height: 22.65px;
}
.index .content .content-item .white-container .link {
          position: absolute;
          left: 10px;
          bottom: 32px;
          width: 113px;
          height: 34px;
          background: #fff;
          border: 1px solid #ff0042;
          border-radius: 53px;
          cursor: pointer;
}
.index .content .content-item .white-container .link .text {
            font-size: 14px;
            color: #1e2022;
}
.index .content .content-item .white-container .link .arrow {
            display: inline-block;
            width: 12px;
            height: 9px;
            background: url("../assets/smArrow_black.png");
            vertical-align: 1px;
}
.index .content .content-item .white-container .link:hover {
            background: #ff0042;
}
.index .content .content-item .white-container .link:hover .text {
              color: #fff;
}
.index .content .content-item .white-container .link:hover .arrow {
              background: url("../assets/smArrow_white.png");
}
.index .content .content-item .white-container .link:active {
            outline: none;
}
.index .content .content-item .title {
        position: absolute;
        top: -35px;
        font-family: "PangMenZhengDao";
        font-size: 0;
}
.index .content .content-item .title.left {
          left: 18px;
}
.index .content .content-item .title.right {
          right: 18px;
}
.index .content .content-item .title .letter {
          display: inline-block;
          font-size: 200px;
          color: #1e2022;
          margin-right: 30px;
}
.index .content .content-item .title .letter:last-child {
            margin-right: 0;
}
.index .content .content-item .title .black {
          color: #1e2022;
}
.index .content .content-item .title .white {
          color: #fff;
}
.index .content .content-blog {
      position: relative;
      height: 400px;
      font-size: 0;
      background: #FFFFFF;
      box-shadow: 0 20px 60px 0 rgba(100, 146, 188, 0.3);
}
.index .content .content-blog .img-container {
        display: inline-block;
        width: 50%;
        height: 100%;
        vertical-align: top;
}
.index .content .content-blog .img-container .img {
          width: 570px;
          height: 400px;
}
.index .content .content-blog .text-container {
        position: relative;
        display: inline-block;
        box-sizing: border-box;
        width: 50%;
        height: 100%;
        padding: 0 40px;
}
.index .content .content-blog .text-container .text {
          margin-top: 140px;
          font-size: 14px;
          color: #323232;
}
.index .content .content-blog .text-container .link {
          display: inline-block;
          position: absolute;
          left: 40px;
          bottom: 37px;
          width: 113px;
          height: 34px;
          line-height: 34px;
          text-align: center;
          font-size: 13.33px;
          background: #fff;
          border: 1px solid #ff0042;
          border-radius: 53px;
          cursor: pointer;
}
.index .content .content-blog .text-container .link .text {
            font-size: 14px;
            color: #1e2022;
}
.index .content .content-blog .text-container .link .arrow {
            display: inline-block;
            width: 12px;
            height: 9px;
            background: url("../assets/smArrow_black.png");
            vertical-align: 1px;
}
.index .content .content-blog .text-container .link:hover {
            background: #ff0042;
}
.index .content .content-blog .text-container .link:hover .text {
              color: #fff;
}
.index .content .content-blog .text-container .link:hover .arrow {
              background: url("../assets/smArrow_white.png");
}
.index .content .content-blog .title {
        position: absolute;
        left: 0;
        right: 0;
        top: 46px;
        margin: auto;
        width: 340px;
        height: 60px;
        box-shadow: 0 20px 60px 0 rgba(100, 146, 188, 0.3);
        font-family: 'PangMenZhengDao';
        font-size: 48px;
        color: #1e2022;
        background: #fff;
        text-align: center;
}



