
.project {
  position: absolute;
  width: 100%;
}
.project .content {
    box-sizing: border-box;
    width: 1200px;
    margin: auto;
    padding: 60px 30px 150px 30px;
}
.project .content .intro {
      width: 784px;
      margin: auto;
      margin-bottom: 100px;
}
.project .content .intro .title {
        text-align: center;
        font-weight: normal;
        font-size: 20px;
        color: #c1c7d4;
        margin-bottom: 30px;
}
.project .content .intro .text {
        font-size: 14px;
        color: #323232;
        line-height: 22.65px;
}
.project .content .project-container .title {
      font-size: 20px;
      color: #c1c7d4;
      text-align: center;
      margin-bottom: 50px;
}
.project .content .project-container .row {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: flex-start;
              flex-direction: flex-start;
      margin-bottom: 50px;
}
.project .content .project-container .row:last-child {
        margin-bottom: 0;
}
.project .content .project-container .item {
      display: inline-block;
      width: 355px;
      height: 490px;
      box-shadow: 0 5px 40px 0 rgba(100, 146, 188, 0.3);
      background: #fff;
      margin-right: 37px;
      transition: 0.2s;
      cursor: pointer;
}
.project .content .project-container .item.unclickable {
        cursor: default;
}
.project .content .project-container .item:last-child {
        margin-right: 0;
}
.project .content .project-container .item:hover {
        box-shadow: 0 20px 60px 0 rgba(100, 146, 188, 0.3);
        -webkit-transform: translate(0, -10px);
                transform: translate(0, -10px);
}
.project .content .project-container .item .img-container {
        height: 270px;
        background: #8DB8EB;
}
.project .content .project-container .item .img-container .img {
          width: 355px;
          height: 270px;
}
.project .content .project-container .item .text-container {
        padding: 30px;
}
.project .content .project-container .item .text-container .title {
          text-align: left;
          font-size: 20px;
          color: #323232;
          margin-bottom: 15px;
          line-height: 20px;
}
.project .content .project-container .item .text-container .text {
          font-size: 14px;
          color: #c1c7d4;
          line-height: 22.65px;
}



