
.footer {
  min-width: 1200px;
  height: 330px;
  background: #1e2022;
}
.footer .footer-content {
    position: relative;
    width: 1140px;
    margin: auto;
    padding-top: 50px;
}
.footer .footer-content .bar {
      position: absolute;
      width: 2px;
      height: 100px;
      background: #ff0042;
}
.footer .footer-content .bar.left {
        left: 0;
}
.footer .footer-content .bar.right {
        right: 0;
}
.footer .footer-content .nav {
      float: left;
      margin-left: 22px;
}
.footer .footer-content .nav .item {
        height: 20px;
        margin-bottom: 20px;
}
.footer .footer-content .nav .item .link {
          font-size: 20px;
          color: #96a0aa;
          cursor: pointer;
}
.footer .footer-content .nav .item .link.active, .footer .footer-content .nav .item .link:hover {
            color: #ff0042;
}
.footer .footer-content .connect {
      float: right;
      margin-right: 22px;
}
.footer .footer-content .connect .item {
        text-align: right;
        height: 20px;
        margin-bottom: 20px;
        font-size: 20px;
        color: #96a0aa;
}
.footer .footer-content .connect .item .QR {
          display: inline-block;
          width: 100px;
          height: 100px;
}
.footer .footer-content .connect .item .QR .qr-img {
            width: 100px;
            height: 100px;
}



