
body {
  font-family: "Microsoft Yahei", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  margin: 0;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
}
button {
  padding: 0;
  background: transparent;
  font-family: "Microsoft Yahei", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}
input,
textarea {
  font-family: "Microsoft Yahei", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}
p,
h1,
h2,
h3,
h4 {
  margin: 0;
  font-weight: normal;
}
pre {
  height: auto;
  margin: 0;
  white-space: pre-wrap;
  word-wrap: break-word;
}
#app {
  min-width: 1200px;
}
.header {
  position: relative;
}
.header .meun-container {
    position: absolute;
    width: 100%;
    background: rgba(0, 0, 0, 0.4);
    z-index: 1;
}
.header .meun-container .content {
      width: 1200px;
      margin: auto;
}
.header .meun-container .content .meun {
        float: right;
        font-size: 0;
}
.header .meun-container .content .meun .meun-item {
          position: relative;
          display: inline-block;
          margin-right: 20px;
}
.header .meun-container .content .meun .meun-item .link {
            display: inline-block;
            width: 100px;
            height: 50px;
            line-height: 50px;
            text-align: center;
            color: #96a0aa;
            text-decoration: none;
            font-size: 14px;
            transition: 0.3s;
            cursor: pointer;
}
.header .meun-container .content .meun .meun-item .link.router-link-exact-active, .header .meun-container .content .meun .meun-item .link:hover {
              color: #fff;
}
.header .background-container {
    position: relative;
    padding-bottom: 41.6667%;
}
.header .background-container .background {
      position: absolute;
      width: 100%;
      height: 100%;
}
.headerFade-enter-active {
  transition: opacity 0.5s;
  opacity: 1;
}
.headerFade-enter {
  opacity: 0;
}
.headerFade-leave-active {
  transition: opacity 0.5s;
  opacity: 0;
}
.headerFade-leave {
  opacity: 1;
}
.fade-enter-active {
  transition: opacity 0.5s;
  transition-delay: 0.5s;
  opacity: 1;
}
.fade-enter {
  opacity: 0;
  position: absolute;
}
.fade-leave-active {
  transition: opacity 0.5s;
  opacity: 0;
}
.fade-leave {
  opacity: 1;
}



